import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Activity } from 'src/app/core/models/activity';
import { first } from 'rxjs/operators';
import { CommitteeService } from 'src/app/core/services/committee.service';
import { AuthenticationService } from 'src/app/core/services/authentication.service';
import { Member } from 'src/app/core/models/member';
import { DatePipe } from '@angular/common';
import { cloneDeep } from 'lodash';
import {EurToCentsPipe} from 'src/app/shared/pipes/eur-to-cents.pipe';
import {CentsToEurPipe} from '../../../../../shared/pipes/cents-to-eur.pipe';

@Component({
  selector: 'app-activity-form',
  templateUrl: './activity-form.component.html',
  styleUrls: ['./activity-form.component.scss']
})
export class ActivityFormComponent implements OnInit {
  description: string;
  activityForm: FormGroup;
  submitted: boolean = false;
  formDirty: boolean = false;
  _activity: Activity;

  @Output() save: EventEmitter<any> = new EventEmitter();

  @Input()
  set activity(activity: Activity) {
    if (activity) {
      this._activity = activity;
      this.activityForm.patchValue(activity);
      this.activityForm.patchValue({
        price: new CentsToEurPipe().transform(activity.price_cents),
        start_date: this.toDateObj(activity.start_date),
        end_date: this.toDateObj(activity.end_date),
        registration_closes: this.toDateObj(activity.registration_closes),
        registration_opens: this.toDateObj(activity.registration_opens)
      })
      this.activityForm.patchValue({ committee_id: activity.committee.id });
      this.listenOnFormChanges();
    }
  }

  //For markdown editor
  editorOptions: object = {
    showBorder: true,
    scrollPastEnd: 2,
    enablePreviewContentClick: false,
  };

  committees = [];

  constructor(
    private formBuilder: FormBuilder,
    private committeeService: CommitteeService,
    private authService: AuthenticationService,
  ) {
    this.activityForm = this.formBuilder.group({
      title: ['', Validators.required],
      description: ['', Validators.required],
      location: ['', Validators.required],
      start_date: [new Date(), Validators.required],
      end_date: ['', Validators.required],
      price: ['', [Validators.required, Validators.pattern('^\\d+(?:\\.\\d{0,2})?$')]],
      committee_id: ['', Validators.required],
      registration_opens: [, Validators.required],
      registration_closes: [, Validators.required]
    })
  }

  ngOnInit() {
    const member = this.authService._getUser();
    if (!member) {
      return;
    }
    this.getCommittees(member);
  }

  listenOnFormChanges() {
    this.activityForm.valueChanges.pipe(
      first(),
    ).subscribe(value => {
      this.formDirty = true;
    })
  }

  getCommittees(member: Member) {
    this.committeeService.committees.subscribe(
      committees => {
        this.committees = committees;
      }
    )
  }

  /*
    Transforms a string format of a date
    to a Date object acceptable by datetime-picker.
  */
  toDateObj(date: string) {
    return new Date(date);
  }

  /*
    Formats a date object to a string format
    acceptable by back-end.
  */
  format(date: Date) {
    return new DatePipe('en-GB').transform(date, 'yyyy-MM-dd HH:mm:ss', 'GMT+1')
  }

  get f() {
    return this.activityForm.controls;
  }

  onSave() {
    this.submitted = true;

    if (this.activityForm.invalid) {
      return;
    }

    let activity = cloneDeep(this.activityForm.value);
    if (this._activity) {
      activity.id = this._activity.id;
    }

    activity.price_cents = new EurToCentsPipe().transform(activity.price);
    delete activity.price;
    activity.start_date = this.format(activity.start_date);
    activity.end_date = this.format(activity.end_date);
    activity.registration_opens = this.format(activity.registration_opens);
    activity.registration_closes = this.format(activity.registration_closes);
    this.save.emit(activity);
  }

}
