import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { ActivityOption } from 'src/app/core/models/activity';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-option-form',
  templateUrl: './option-form.component.html',
  styleUrls: ['./option-form.component.scss']
})
export class OptionFormComponent implements OnInit {
  option_types = ['text', 'yes_no', 'number', 'datetime'];
  optionsForm: FormGroup;
  private _option: ActivityOption;
  @Output() submit: EventEmitter<any> = new EventEmitter();
  @Input() action: string;
  @Input() 
  set option(option: ActivityOption) {
    this._option = option
    this.optionsForm.patchValue(this._option)
  };

  submitted = false;

  constructor(private formBuilder: FormBuilder) { 
    this.optionsForm = this.formBuilder.group({
      question: ['', Validators.required],
      type: ['text', Validators.required],
      required: [false]
    });
  }

  ngOnInit() {
  }

  get f() {
    return this.optionsForm.controls;
  }

  onSubmit() {
    this.submitted = true;

    if(this.optionsForm.invalid){
      return;
    }
    let optionObj: ActivityOption = this.optionsForm.value;
    if(this.action == 'edit'){
       optionObj.id = this._option.id;
    }

    this.submit.emit(optionObj);

    this.optionsForm.reset({
      question: '',
      type: '',
      required: false
    });
    this.submitted = false;
  }
}
