import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityManagementComponent } from './activity-management.component';
import { AddActivityComponent } from './pages/add-activity/add-activity.component';
import { ActivityOverviewComponent } from './pages/activity-overview/activity-overview.component';
import { ActivityWrapperComponent } from './pages/activity-wrapper/activity-wrapper.component';
import { ActivityRegistrationsComponent } from './pages/activity-registrations/activity-registrations.component';

const routes: Routes = [
  {
    path: '', component: ActivityManagementComponent
  },
  {
    path: 'add', component: AddActivityComponent
  },
  {
    path: 'columns',
    loadChildren: () => import('../columns/columns.module').then(m => m.ColumnsModule),
    data: {
      model: 'activities'
    }
  },
  {
    path: ':id/edit',
    component: ActivityWrapperComponent,
    children: [
      {
        path: '',
        component: ActivityOverviewComponent
      },
      {
        path: 'registrations',
        component: ActivityRegistrationsComponent
      }

    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityManagementRoutingModule { }
